<template>
  <div class="content">
    <myhead/>
    <div class="images">
      <img :src="this.keydata.img" alt="" class="imgDetail">
    </div>
    <div class="title">
      <h3>{{this.keydata.name}}</h3>
      <p>{{this.keydata.title}}</p>
    </div>
    <div class="artick">
      <h3>简介</h3>
      <p>{{this.keydata.content}}</p>
    </div>
  </div>
</template>

<script>
  import myhead from '@/components/myhead'
  import mock from '@/mock'
  import axios from 'axios'
  export default {
    data(){
      return {
        keydata:'',
        id:''
      }
    },
    components:{
      myhead
    },
    mounted(){
      localStorage.id = this.$route.params.id;
      this.getJs()
    },
    methods:{
      getJs(){
        this.id = localStorage.id || this.$route.params.id;
        axios.get('/api/bannerdetail').then(res => {
          for(let key in res.data) {
            if(key === this.$route.params.id){
              this.keydata = res.data[key]
            }
          } 
        })
      }
    }
  }
</script>

<style scoped>
.content{
  width:100%;
  height:100%;
  overflow-y:scroll;
}
.imgDetail{
  width:100%;
  height:auto;
}
.title{
  padding:1rem;
}
h3{
  font-size:1.6rem;
  padding-top:.5rem;
  color:rgb(190, 43, 87);
}
p{
  font-size:1.2rem;
  text-indent:2rem;
  padding-top:.5rem;
  padding-bottom:.5rem;
}
.artick{
  padding:1rem;
}
</style>


